var results = new ReactiveVar();
var progress = new ReactiveVar('');

Template.importCorePFMEA.events({
  "click #preview": function (e) {
    var delimiter;
    var limitPreview;
    var temp;
    var nRows;
    e.preventDefault();
    delimiter = $("input[name=delimiter]:checked").val();
    limitPreview = 0;
    if ($('input[name=limitPreview]').prop('checked')) {
      limitPreview = 10;
    }
    temp = Papa.parse($("#csv").val(), {
      header: false,
      preview: limitPreview,
      delimiter: delimiter,
    });
    results.set(temp.data);
    nRows = temp.data.length;
    progress.set("Showing preview for " + nRows + " rows");
    return false;
  },
  "click #import": function (e) {
    var count;
    var delimiter;
    var i;
    var len;
    var rd;
    var row;
    var shareDialogInfo;
    var temp;
    var headersOrder = {
      //"stepNumber": 0,
      "processStepDesignation": 0,
      "partType": 1,
      "machineType": 2,
      "potentialFailureMode": 3,
      "effectOfFailure": 4,
      "S": 5,
      "specialCharacteristic": 6,
      "potentialCauseOfFailure": 7,
      "currentDesignPrevention": 8,
      "O": 9,
      "currentDesignDetection": 10,
      "D": 11,
    }; //first col is 0 !
    e.preventDefault();
    $('#import').prop('disabled', true);
    delimiter = $("input[name=delimiter]:checked").val();
    shareDialogInfo = {
      template: Template.spinner,
      title: "Wait",
      modalDialogClass: "wait-dialog",
      modalBodyClass: "share-modal-body",
      modalFooterClass: "share-modal-footer",
      removeOnHide: true,
      buttons: {},
    };
    rd = ReactiveModal.initDialog(shareDialogInfo);
    rd.show();
    progress.set('Please wait...');
    temp = Papa.parse($("#csv").val(), {
      header: false,
      delimiter: delimiter,
    }).data;
    count = 0;
    if ((temp !== null ? temp.length : void 0) > 0) {
      for (i = 0, len = temp.length; i < len; i++) {
        row = temp[i];
        if (Meteor.settings.public.debug) {
          console.log(row);
        }
        Meteor.call("importCorePFMEALine", row, headersOrder, function (error, id) {
          count++;
          nRows = temp.length;
          console.log("Importation " + count + "/" + nRows);
          progress.set("Importation " + count + "/" + nRows);
          if (error) {
            if (typeof(toastr) !== 'undefined') {
              toastr.error(error.reason);
            }
          }
          if (count >= nRows) {
            rd.hide();
            return Router.go("corePFMEA");
          }
        });
      }
    }
    else {
      rd.hide();
      progress.set('');
    }
    return false;
  },
  "click #cancel": function (el) {
    el.preventDefault();
    return results.set({});
  },
});

Template.importCorePFMEA.helpers({
  data: function () {
    return results.get();
  },
  selectedCategory: function () {
    return Session.get("selectedCategory");
  },
  progress: function () {
    return progress.get();
  },
  hasPreview: function () {
    return results.get();
  },
  headers: function () {
    var res;
    res = results.get();
    if (res && res[0]) {
      return Object.keys(res[0]);
    }
  },
  categories: function () {
    return Categories.find();
  },
  keys: function () {
    var key;
    var res;
    var value;
    res = [];
    for (key in this) {
      if ({}.hasOwnProperty.call(this, key)) {
        value = this[key];
        res.push({
          key: key,
          value: value,
        });
      }
    }
    return res;
  },
});

Template.importCorePFMEA.rendered = function () {
  return $("#selectedCategory").val(Session.get("selectedCategory"));
};
