if (Meteor.settings.public.show.projects) {
  Router.route("/fmea/p/core", {
    name: "corePFMEA",
    waitOn: function () {
      return [
        Meteor.subscribe("corePFMEA"),
      ];
    },
  });

  Router.route("/fmea/p/core/import", {
    name: "importCorePFMEA",
  });

  Router.route("/fmea/p/core/editLine/:_id", {
    name: "editPFMEALine",
    data: function () {
      return corePFMEA.findOne(this.params._id);
    },
    waitOn: function () {
      return [
        Meteor.subscribe("corePFMEALine", this.params._id),
       ];
    },
  });


  Router.route("/fmea/settings", {
    name: "settingsFMEA",
    waitOn: function () {
      return [
        Meteor.subscribe("settingsfmea"),
       ];
    },
  });


  urlCorePFMEA = Router.routes.corePFMEA.path();
  menuHTML = new Spacebars.SafeString(
     '<li role="separator" class="divider"></li>'
      + '<li class="dropdown-header">FMEA</li>'
      + '<li><a href="' + urlCorePFMEA + '" title="PFMEA">P-FMEA</a></li>'
      + '<li><a href="#" title="coming soon - DFMEA">D-FMEA</a></li>'
      + '<li role="separator" class="divider"></li>'
  );

  RKCore.packageMenu.push(
    {
      "menuHTML": menuHTML,
      "fromPackage": "rationalk:fmea",
    }
  );
}
