Template.corePFMEA.events({
	"click a.createCorePFMEALine": function (e) {
	    e.preventDefault();
	    Meteor.call('createCorePFMEALine');
			return false;
	},
	"click a.clearCorePFMEA": function (e) {
			e.preventDefault();
			Meteor.call('clearCorePFMEA');
			return false;
	},
	"click a.deleteLink": function (e) {
	    e.preventDefault();
	    Meteor.call('deleteCorePFMEALine', e.currentTarget.dataset.lineid);
			return false;
	},
	"change .processStepDesignation-all": function (e) {
		$(".processStepDesignation").prop('checked', e.target.checked);
	},
	"change .partType-all": function (e) {
		$(".partType").prop('checked', e.target.checked);
	},
	"change .machineType-all": function (e) {
		$(".machineType").prop('checked', e.target.checked);
	},
	"submit form#filterPFMEA": function (e) {
		var selectedProcessSteps = [];
		var selectedPartTypes = [];
		var selectedMachineTypes = [];
		var rationalkPFMEA = {};
		rationalkPFMEA.selectedProcessSteps = [];
		rationalkPFMEA.selectedPartTypes = [];
		rationalkPFMEA.selectedMachineTypes = [];
		e.preventDefault();
		$("input:checkbox.processStepDesignation[type=checkbox]:checked").each(function () {
			selectedProcessSteps.push($(this).val());
		});
		$("input:checkbox.partType[type=checkbox]:checked").each(function () {
			selectedPartTypes.push($(this).val());
		});
		$("input:checkbox.machineType[type=checkbox]:checked").each(function () {
			selectedMachineTypes.push($(this).val());
		});
		rationalkPFMEA.selectedProcessSteps = selectedProcessSteps;
		rationalkPFMEA.selectedPartTypes = selectedPartTypes;
		rationalkPFMEA.selectedMachineTypes = selectedMachineTypes;
		if (Meteor.settings.public.debug) {
			console.log(rationalkPFMEA);
		}
		Session.set('rationalkPFMEA', rationalkPFMEA);
	},
	"click #export": function (el) {
    var config;
		var count;
		var csv;
		var data;
		var delimiter;
		var hasQuotes;
		var rd;
		var shareDialogInfo;
		var rationalkPFMEA = Session.get("rationalkPFMEA");
		el.preventDefault();
    $("#csv").val('');
    delimiter = $("input[name=delimiter]:checked").val();
    hasQuotes = $('input[name=quotes]').prop('checked');
    shareDialogInfo = {
      template: Template.spinner,
      title: "Wait",
      modalDialogClass: "wait-dialog",
      modalBodyClass: "share-modal-body",
      modalFooterClass: "share-modal-footer",
      removeOnHide: true,
      buttons: {},
    };
    rd = ReactiveModal.initDialog(shareDialogInfo);
    rd.show();
    progress.set('Please wait...');

		if (typeof rationalkPFMEA.selectedProcessSteps !== 'undefined') {
			data = corePFMEA.find(
				{
					$and: [
						{
							processStepDesignation: {
								$in: rationalkPFMEA.selectedProcessSteps,
							},
						},
						{
							partType: {
								$in: rationalkPFMEA.selectedPartTypes,
							},
						},
						{
							machineType: {
								$in: rationalkPFMEA.selectedMachineTypes,
							},
						},
					],
				},
				{
			    fields: {
			        importDate: 0,
			        _id: 0,
							importedBy: 0,
							full: 0,
							searchResultFromCorePFMEA: 0,
			     }
				}
			).fetch();

			config = {
	      delimiter: delimiter,
	      quotes: hasQuotes,
	    };
	    csv = Papa.unparse(data, config);
	    $('#csv').val(csv);
	    count = 0;
	    rd.hide();
	    progress.set('');
		}
    return false;
  },
});
