PFMEA = new Mongo.Collection('pfmea');
corePFMEA = new Mongo.Collection('corepfmea');
SettingsFMEA = new Mongo.Collection('settingsfmea');

PFMEA.allow( {
		insert: function (userId) {return !! userId; },
		update: function (userId) {return !!userId; },
    remove: function (userId) {return !!userId; },
});

corePFMEA.allow( {
		insert: function (userId) {return !! userId; },
		update: function (userId) {return !!userId; },
    remove: function (userId) {return !!userId; },
});

SettingsFMEA.allow( {
		insert: function (userId) {return !! userId; },
		update: function (userId) {return !!userId; },
    remove: function (userId) {return !!userId; },
});

if (Meteor.isServer) {
	if (typeof corePFMEA.createIndex === 'function') {
		corePFMEA.createIndex({ full: "text" }, { name: "TextIndex" });
	}
	else {
		if (typeof corePFMEA._ensureIndex === 'function') {
			corePFMEA._ensureIndex( { full: "text" }, {name: "TextIndex"});
	}
}
} //end if Server
