RKFMEA = {};
RKFMEA.Collections = {};
RKFMEA.Collections.corePFMEA = corePFMEA;
RKFMEA.corePFMEA = {};
RKFMEA.coreDFMEA = {}; //no used

Meteor.methods({
	createCorePFMEALine: function () {
		corePFMEA.insert(
			{
				processStepDesignation: "Turning",
				partType: "Inner Ring",
				machineType: "CNC",
			  potentialFailureMode: "The inner diameter is too small",
				effectOfFailure: "Trash",
				potentialCauseOfFailure: "Tool broken",
				currentDesignPrevention: "Maintenance programm",
				currentDesignDetection: "SPC",
				S:6,
				D:2,
				O:3,
				full: "Turning Inner Ring CNC The inner diameter is too small Trash Tool broken Maintenance programm SPC",
				searchResultFromCorePFMEA: true,
			}
		);
		if (typeof(toastr) !== 'undefined') {
			toastr.success('A new line has been added successfully');
		}
	},
	deleteCorePFMEALine: function (lineId) {
		check(lineId, String);
		corePFMEA.remove(
			{
			  _id: lineId,
			}
		);
		if (typeof(toastr) !== 'undefined') {
			toastr.success('Line deleted succesfully');
		}
	},
	updateCorePFMEALine: function (data) {
		var full = '';
		check(data, Match.Any);
		full = full.concat(data.processStepDesignation).concat(" ");
		full = full.concat(data.partType).concat(" ");
		full = full.concat(data.machineType).concat(" ");
		full = full.concat(data.potentialFailureMode).concat(" ");
		full = full.concat(data.effectOfFailure).concat(" ");
		full = full.concat(data.potentialCauseOfFailure).concat(" ");
		full = full.concat(data.currentDesignPrevention).concat(" ");
		full = full.concat(data.currentDesignDetection).concat(" ");

		if (Meteor.settings.public.debug) {
			console.log("full : " + full);
		}
		corePFMEA.update(
			{
			  $and: [
			  	{
						_id: data.lineId,
					},
			  ],
			},
			{
					// stepNumber: data.stepNumber,
		      processStepDesignation: data.processStepDesignation,
		      partType: data.partType,
		      machineType: data.machineType,
		      potentialFailureMode: data.potentialFailureMode,
		      effectOfFailure: data.effectOfFailure,
		      S: data.S,
		      specialCharacteristic: data.specialCharacteristic,
		      potentialCauseOfFailure: data.potentialCauseOfFailure,
		      currentDesignPrevention: data.currentDesignPrevention,
		      O: data.O,
		      currentDesignDetection: data.currentDesignDetection,
		      D: data.D,
					full: full,
					searchResultFromCorePFMEA: true,
			},
			{
			    upsert: true,
			}
		);
		if (typeof(toastr) !== 'undefined') {
			toastr.success('The line has been updated successfully');
		}
	},
});

RKFMEA.corePFMEA.findAll = function () {
  return corePFMEA.find({}, {sort: {score: -1}}).fetch();
};

RKFMEA.corePFMEA.findFullText = function (searchQuery) {
  return corePFMEA.find( {
        $text: {
          $search: searchQuery,
        },
    }, {
        fields: { score: { $meta: 'textScore' } },
        sort: { score: { $meta: 'textScore' } },
        limit: 30,
    });
};

RKFMEA.corePFMEA.findDummy = function () {
  return corePFMEA.find({$text: { $search: "somethingthatyouwillneverfind" }});
};
