Template.editPFMEALine.rendered = function () {
};

Template.editPFMEALine.helpers({
});

Template.editPFMEALine.events({
	'submit form': function (e) {
		var data = {};
	  e.preventDefault();
		data.lineId = this._id;
		//data.stepNumber = e.target.stepNumber.value;
		data.processStepDesignation = e.target.processStepDesignation.value;
		data.partType = e.target.partType.value;
	  data.machineType = e.target.machineType.value;
		data.potentialFailureMode = e.target.potentialFailureMode.value;
		data.effectOfFailure = e.target.effectOfFailure.value;
		data.S = e.target.S.value;
		data.specialCharacteristic = e.target.specialCharacteristic.value;
		data.potentialCauseOfFailure = e.target.potentialCauseOfFailure.value;
		data.currentDesignPrevention = e.target.currentDesignPrevention.value;
		data.O = e.target.O.value;
		data.currentDesignDetection = e.target.currentDesignDetection.value;
		data.D = e.target.D.value;
	  Meteor.call('updateCorePFMEALine', data, function () {});
	},
	"click a.delete": function (event) {
	    event.preventDefault();
	    console.log(this);
	    Meteor.call('deleteProject', this._id, function () {});
			return false;
	},
});
